export default {
  namespaced: true,

  state: {
    selectedTheme: 0,
    themes: [
      { name: "Grey", color: "grey" },
      { name: "Blue", color: "blue" },
      { name: "Teal", color: "teal" },
      { name: "Pink", color: "pink" },
      { name: "Green", color: "green" },
      { name: "Purple", color: "purple" },
      { name: "Orange", color: "orange" },
      { name: "Brown", color: "brown" },
    ],
  },

  getters: {},

  mutations: {
    setSelectedTheme(state, num) {
      state.selectedTheme = num;
    },
  },

  actions: {
    // The choice of color theme.
    changeColorTheme({ rootState }, payload) {
      const color = payload.color;
      const themes = payload.themes;
      let theme = rootState.colorThemes.light[color];
      themes.light.primary = theme.primary;
      themes.light.primaryTwo = theme.primaryTwo;
      themes.light.btnPrimary = theme.btnPrimary;
      theme = rootState.colorThemes.dark[color];
      themes.dark.primary = theme.primary;
      themes.dark.primaryTwo = theme.primaryTwo;
      themes.dark.btnPrimary = theme.btnPrimary;
    },
  },
};
