import Vue from "vue";

export default {
  namespaced: true,

  state: {
    documents: [],
    currentPageNumber: 1,
    pageCount: 0,
    progressionStep: 1,
    docsPerPage: 50,
    sortDocList: "alphabetical_links",
    sortDirectDocList: -1,
    sortTypes: ["alphabetical_links", "created", "updated"],
    searchQuery: null,
    // Data of filter by categories.
    dataFilter: [],
    selectedDataFilter: {},
  },

  getters: {},

  mutations: {
    setDocuments(state, arr) {
      state.documents = arr;
    },
    setCurrentPageNumber(state, num) {
      state.currentPageNumber = num;
    },
    setPageCount(state, num) {
      state.pageCount = num;
    },
    setProgressionStep(state, num) {
      state.progressionStep = num;
    },
    setDocsPerPage(state, num) {
      state.docsPerPage = num;
    },
    setSortDocList(state, name) {
      state.sortDocList = name;
    },
    setSortDirectDocList(state, num) {
      state.sortDirectDocList = num;
    },
    setSearchQuery(state, text) {
      state.searchQuery = text;
    },
    setDataFilter(state, arr) {
      state.dataFilter = arr;
    },
    setSelectedDataFilter(state, obj) {
      state.selectedDataFilter = obj;
    },
  },

  actions: {
    // Get a list of documents.
    ajaxGetDocumentList({ state, commit, rootState }, payload) {
      return new Promise((resolve, reject) => {
        const collection =
          rootState.serviceList[payload.indexService].collections[
            payload.indexCollection
          ];
        const filter = {};
        for (let idx = 0; idx < state.dataFilter.length; idx++) {
          const df = state.dataFilter[idx];
          const key = df.field;
          const value = state.selectedDataFilter[key];
          if (value !== null) {
            if (Array.isArray(value)) {
              if (value.length > 0) {
                filter[key] = {
                  value: JSON.stringify(value.map(String)),
                  negation: df.negation,
                };
              }
            } else {
              filter[key] = {
                value: String(value),
                negation: df.negation,
              };
            }
          }
        }
        const payloadQuery = {
          model_key: collection.model_key,
          fields_name: collection.fields.map((item) => item.field),
          page_num: state.currentPageNumber,
          search_query: state.searchQuery || "",
          limit: state.docsPerPage,
          sort: state.sortDocList,
          direct: state.sortDirectDocList,
          filter: Object.keys(filter).length > 0 ? JSON.stringify(filter) : "",
        };
        Vue.axios
          .post("/admin/document-list", payloadQuery)
          .then((response) => {
            const data = response.data;
            if (!data.is_authenticated) {
              commit("setIsAuthenticated", false, { root: true });
            } else {
              commit("setPageCount", data.page_count);
              commit("setDocuments", data.documents);
              resolve();
            }
          })
          .catch((error) => {
            window.console.log(error);
            reject(error);
          });
      });
    },
    // Get data of filter by categories.
    ajaxGetDataFilter({ commit, rootState }, payload) {
      return new Promise((resolve, reject) => {
        const collection =
          rootState.serviceList[payload.indexService].collections[
            payload.indexCollection
          ];
        const payloadQuery = {
          model_key: collection.model_key,
        };
        Vue.axios
          .post("/admin/get-filter", payloadQuery)
          .then((response) => {
            const data = response.data;
            if (!data.is_authenticated) {
              commit("setIsAuthenticated", false, { root: true });
            } else {
              const filter = data.filter;
              const selectedDataFilter = {};
              filter.sort(function (item, item2) {
                if (item.label > item2.label) {
                  return 1;
                }
                if (item.label < item2.label) {
                  return -1;
                }
                return 0;
              });
              for (const item of filter) {
                selectedDataFilter[item.field] = null;
              }
              commit("setDataFilter", filter);
              commit("setSelectedDataFilter", selectedDataFilter);
              resolve();
            }
          })
          .catch((error) => {
            window.console.log(error);
            reject(error);
          });
      });
    },
    // Reset the values of all default parameters.
    resetToDefault({ commit }) {
      commit("setDocuments", []);
      commit("setCurrentPageNumber", 1);
      commit("setProgressionStep", 1);
      commit("setPageCount", 0);
      commit("setSortDocList", "alphabetical_links");
      commit("setSortDirectDocList", -1);
      commit("setSearchQuery", null);
      commit("setDataFilter", []);
      commit("setSelectedDataFilter", {});
    },
  },
};
