<template>
  <v-app-bar app dense fixed>
    <!-- Menu button. -->
    <v-app-bar-nav-icon
      :style="`color: #{$vuetify.theme.dark ? '#4e5360' : '#000'}`"
      @click="setOpenPanelServiceList(!openPanelServiceList)"
    ></v-app-bar-nav-icon>
    <v-toolbar-title class="btnBlackGrey--text">{{ brand }}</v-toolbar-title>
    <v-spacer></v-spacer>
    <!-- Light or dark theme button. -->
    <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <v-btn icon v-bind="attrs" v-on="on" @click="changeTheme()">
          <v-icon>mdi-brightness-4</v-icon>
        </v-btn>
      </template>
      <span>{{ $t("message.1") }}</span>
    </v-tooltip>
    <!-- The choice of color theme. -->
    <v-menu left bottom offset-y>
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          small
          rounded
          depressed
          color="txtGrey"
          :class="`${
            $vuetify.theme.dark ? 'secondary--text' : 'white--text'
          } mx-2`"
          v-bind="attrs"
          v-on="on"
        >
          {{ $t("message.36") }}
        </v-btn>
      </template>
      <v-list dense>
        <v-list-item-group v-model="compSelectedTheme" color="primaryTwo">
          <v-list-item
            v-for="(theme, idx) in themes"
            :key="idx"
            @click="
              changeColorTheme({
                color: theme.color,
                themes: $vuetify.theme.themes,
              })
            "
          >
            <v-list-item-content>
              <v-list-item-title>{{ theme.name }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-menu>
    <!-- Logout button. -->
    <v-tooltip bottom>
      <template v-slot:activator="{ on, attrs }">
        <v-btn icon v-bind="attrs" v-on="on" @click="logout()">
          <v-icon>mdi-logout</v-icon>
        </v-btn>
      </template>
      <span>{{ $t("message.2") }}</span>
    </v-tooltip>
  </v-app-bar>
</template>

<script>
import { mapState, mapMutations, mapActions } from "vuex";

export default {
  name: "AppBar",

  computed: {
    ...mapState(["brand", "openPanelServiceList", "colorThemes"]),
    ...mapState("appBar", ["selectedTheme", "themes"]),
    compSelectedTheme: {
      get: function () {
        return this.selectedTheme;
      },
      set: function (num) {
        this.setSelectedTheme(num);
      },
    },
  },

  methods: {
    ...mapMutations(["setIsAuthenticated", "setOpenPanelServiceList"]),
    ...mapMutations("popUpMsg", ["setShowMsg"]),
    ...mapMutations("appBar", ["setSelectedTheme"]),
    ...mapActions("appBar", ["changeColorTheme"]),
    logout() {
      this.setShowMsg(false);
      this.axios
        .post("/admin/logout", {})
        .then(() => {
          this.setIsAuthenticated(false);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    changeTheme() {
      this.$vuetify.theme.dark = !this.$vuetify.theme.dark;
    },
  },
};
</script>
