export default {
  // Information about the company
  setLogo(state, url) {
    state.logo = url;
  },
  setBrand(state, name) {
    state.brand = name;
  },
  setSlogan(state, name) {
    state.slogan = name;
  },

  // Access
  setIsAuthenticated(state, flag) {
    state.isAuthenticated = flag;
  },

  // Service list
  setOpenPanelServiceList(state, flag) {
    state.openPanelServiceList = flag;
  },
  setPanelWidthServiceList(state, num) {
    state.panelWidthServiceList = num;
  },
  setSelectedService(state, arr) {
    state.selectedService = arr;
  },
  setServiceList(state, arr) {
    state.serviceList = arr;
  },

  // Current parameters URL address.
  setService(state, name) {
    state.service = name;
  },
  setIndexService(state, num) {
    state.indexService = Number.parseInt(num);
  },
  setCollection(state, name) {
    state.collection = name;
  },
  setIndexCollection(state, num) {
    state.indexCollection = Number.parseInt(num);
  },
};
